.weather-param {
    padding: 40px 0;
}

.param-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
}

.param-card {
    background: #e0e0e0;
    border-radius: 12px;
    padding: 20px;
    text-align: center;
    font-weight: 500;
}